import React from 'react';
import styled from 'styled-components';
import { Button, Title2 } from '../Home/homeStyledComponents';
import { colors, mobileThresholdPixels } from '../styledComponents';

const Container = styled.div`
  position: relative;
  text-align: center;
`;

const InvertedBlock = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 20px 0px;
  text-align: left;

  @media (max-width: ${mobileThresholdPixels}) {
    flex-direction: column;
    padding: 2px 0px;
  }
`;

const Column = styled.div`
  margin: 20px;
  max-width: 45%;
  display: flex;
  flex-direction: column;

  @media (max-width: ${mobileThresholdPixels}) {
    margin: 2px;
    max-width: 100%;
  }
`;

const P = styled.p`
  color: ${colors.navy};
`;

const TheWhy = () => (
  <Container id="TheWhy">
    <Title2>⋅ le pourquoi ⋅</Title2>
    <InvertedBlock>
      <Column>
        <P>{'tilli vous confie des missions près de chez vous et en fonction de vos horaires.'}</P>
        <P>⋅ ⋅ ⋅</P>
        <P>{"les prix sont fixés à l'avance, vous n'avez à négocier avec votre client."}</P>
        <P>⋅ ⋅ ⋅</P>
        <P>{'vos prises de rendez-vous, suivis, factures et paiements sont accessibles depuis l\'app tilli.'}</P>
      </Column>
      <Column>
        <P>{'vous rejoignez notre communauté de couturiers et partagez vos expériences.'}</P>
        <P>⋅ ⋅ ⋅</P>
        <P>{'vous bénéficiez de bons plans, de formations et de petites attentions chaque mois.'}</P>
        <P>⋅ ⋅ ⋅</P>
        <P>{'si vous n\'avez pas d\'assurance, nous vous occupons pour vous.'}</P>
      </Column>
    </InvertedBlock>
    <a
      target="_blank" rel="noreferrer noopener"
      href="https://docs.google.com/forms/d/e/1FAIpQLScvR2ul5h35xj4qXnM0dTLj3cEkaRrmY-7Y9-eZyismNICHog/viewform"
    >
      <Button>{'> je veux être couturier <'}</Button>
    </a>
  </Container>
);

export default TheWhy;



